.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  /* background-color: #282c34; */
  height: 50vh;
  display: flex;
  flex-direction: row;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color:#d6204e;
  border-bottom: 2px solid #d6204e;
}
.App-body {
  height: 50vh;
  padding-left: 32px;
  border-bottom: 2px solid #d6204e;
}
.App-body > div label, input{
   margin-right: 10px;
}
.App-link {
  color: #61dafb;
}
.App-footer {
  height: 10vh;
  text-align: center;
  font-weight: 600;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
